/* eslint-disable no-console */
import Content from '../../components/Content.vue'
import Jobs from '../../components/Jobs.vue'
import MiniHelp from '../../components/MiniHelp.vue'
import SocialLinks from '../../components/SocialLinks.vue'
import SideProjects from '../../components/SideProjects.vue'
import AnimatedSectionsList from '../../content/animated-sections';
import LinksList from '../../content/links';

export default {
  components: {
    Content,
    Jobs,
    MiniHelp,
    SocialLinks,
    SideProjects
  },

  data: () => ({
    charDelay: 4,
    isPrinted: false,
    showJobs: false,
    section: 'home',
    args: '',
    cmd: 'home',
    lastCmd: '',
    historyCmd: [],
    historyPos: 0,
    inputValue: '',
    autocompleteList: [],
    commandList: ['home', 'stack', 'projects', 'business', 'jobs', 'test', 'help', 'ping', 'cowsay', 'pizza', 'game'],
    commandAliases: {
      q: 'q|quit|exit|back|return|leave',
      jobs: 'careers|vc|job|vacancy|vacancies',
      game: 'game|play|fun|egg|snake',
      stack: 'techstack|tech|technology|framework',
      test: 'test',
      github: 'github',
      habr: 'habr',
      vk: 'vk',
      facebook: 'facebook',
      telegram: 'telegram',
      youtube: 'youtube',
    },
    mobile: false
  }),

  computed: {
    lastCmdComputed() {
      if (this.lastCmd) {
        return "> " + this.lastCmd
      }
    },
    isAnimated() {
      return AnimatedSectionsList.includes(this.section);
    },
  },

  methods: {
    sendMetrika(eventId) {
      window.ym.send(eventId, this.$store.devmode ? 'dev' : 'manager');
    },

    checkCmd() {
      if (!this.cmd) return;
      this.lastCmd = this.cmd !== '' ? this.cmd : this.lastCmd;
      const x = this.cmd.trim().split(' ');
      this.cmd = x[0].toLowerCase().replace(/[/-]/g, '');
      this.args = x.splice(1).join(' ');

      const alias = this.commandAliases;

      const aliasActions = {

        [alias.q]:
          (routeTo) => routeTo({ name: 'command', params: { command: 'home' } }),

        [alias.jobs]:
          () => this.cmd = 'jobs',

        [alias.stack]:
          () => this.cmd = 'stack',

        [alias.test]:
          (routeTo) => routeTo({ name: 'test' }),

        [alias.game]:
          (routeTo) => routeTo({ name: 'game' }),

        [alias.github]: () => {
          this.cmd = ''
          window.open(LinksList.githubLink.href, '_blank').focus();
        },
        [alias.habr]: () => {
          this.cmd = ''
          window.open(LinksList.habrLink.href, '_blank').focus();
        },
        [alias.vk]: () => {
          this.cmd = ''
          window.open(LinksList.vkLink.href, '_blank').focus();
        },
        [alias.facebook]: () => {
          this.cmd = ''
          window.open(LinksList.fbLink.href, '_blank').focus();
        },
        [alias.telegram]: () => {
          this.cmd = ''
          window.open(LinksList.tgLink.href, '_blank').focus();
        },
        [alias.youtube]: () => {
          this.cmd = ''
          window.open(LinksList.ytLink.href, '_blank').focus();
        },
      };

      const aliasKey = Object.keys(aliasActions).find(key => key.split('|').some(x => x === this.cmd))
      let isRouted = false;
      const routeTo = (routeParams) => {
        isRouted = true;
        return this.$router.push(routeParams)
      };
      if (aliasKey) {
        aliasActions[aliasKey](routeTo);
        if (isRouted) {
          this.cmd = "";
          return true;
        }
      }
      else if (this.cmd !== '') {
        // this.lastCmd = this.cmd
        this.section = 'notexist'
      }

      //Проверка на основные команды
      if (this.commandList.includes(this.cmd)) {
        // this.lastCmd = this.cmd
        this.section = this.cmd;
        this.isPrinted = false;

        //пушим команды в историю, чтобы стрелками выбирать их
        this.historyCmd.push(this.cmd);
        this.historyPos = this.historyCmd.length;

        if (this.$route.params.command !== this.cmd)
          this.$router.push({ name: 'command', params: { command: this.cmd } })
      }

      //Очищаем переменную и инпут
      this.cmd = "";
      return true;
    },

    autocomplete(event) {
      event.preventDefault();
      const command = this.cmd.toLowerCase().trim();
      const suggestion = this.autocompleteList.find(c => (c.length > command.length) && c.startsWith(command));
      if (!suggestion) return;
      this.cmd = suggestion;
    },

    focusInput() {
      if (this.mobile !== true && this.$refs.myInput) {
        this.$refs.myInput.focus();
      }
    },

    skipTyping() {
      this.charDelay = 0;
      setTimeout(() => this.charDelay = 4, 50);
    },

    prevCmd() {
      if (this.historyPos > 0) this.historyPos--;

      const i = this.historyPos;
      this.cmd = this.historyCmd[i];
    },

    nextCmd() {
      if (this.historyPos < this.historyCmd.length) this.historyPos++;

      const i = this.historyPos;
      this.cmd = this.historyCmd[i];
    },

    caretPos() {
      const caretPos = (this.$refs.myInput.value.length - this.$refs.myInput.selectionStart) * 9;

      this.$refs.caret.style.transform = 'translateX(-' + caretPos + 'px)';
    },

    scrollTop() {
      const scrollArea = this.$refs.developer;

      return setTimeout(function () {
        scrollArea.scrollTo(0, 0);
      }, 20);
    },

    scrollDown() {
      const scrollArea = this.$refs.developer;

      return setTimeout(function () {
        scrollArea.scrollTo(0, 1500);
      }, 30);
    },

    isPrintDone() {
      this.isPrinted = true;
    },

    mobileCheck() {
      const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
      if (isMobile) {
        this.mobile = true;
      } else {
        this.mobile = false;
      }
    }
  },

  watch: {
    '$route'(to, from) {
      this.cmd = to.params.command;
      if (this.cmd) {
        if (this.args) this.cmd += ' ' + this.args;
        this.checkCmd();
        this.scrollTop();
      }

      if (to.name == 'commandsEmpty') {
        this.section = 'home'
        this.lastCmd = ''
      }
    },
  },

  created() {
    this.autocompleteList = [...this.commandList];
    Object.values(this.commandAliases).forEach(alias =>
      alias.split('|').forEach(a => this.autocompleteList.push(a))
    );
    this.cmd = this.$route.params.command;
    if (this.cmd)
      this.checkCmd();
  },

  updated() {
    this.mobileCheck();
  },

  mounted() {
    this.mobileCheck();
    this.focusInput();

    window.onfocus = () => {
      this.focusInput()
    };

    document.addEventListener('keyup', () => {
      if (this.section === 'game') return;
      if (this.$refs.myInput === document.activeElement)
        return;
      if (event.key && event.key.length === 1 && (typeof this.cmd === 'string'))
        this.cmd += event.key;
      this.focusInput();
    });
  }
}